.hero-room {
	&__block {
		border-bottom: 1px dashed #ababab;
		padding-bottom: 27px;
		margin-bottom: 27px;
		@include clr; }
	&__room-picture {
		float: left;
		max-width: 212px;
		width: 100%;
		@include md {
			float: none; } }
	&__info {
		max-width: 917px;
		float: right;
		@include md {
			float: none; } }
	&__title {
		margin-bottom: 5px; }
	&__info-head {
		display: flex;
		justify-content: space-between; }
	&__info-bottom {
		display: flex;
		align-items: center;
		@include sm {
			flex-direction: column;
			align-items: flex-start; } }
	&__price {
		color: #000000;
		font-size: 21px;
		line-height: 20px;
		@include sm {
			display: none; } }
	&__list {
		padding-bottom: 10px;
		border-bottom: 1px dashed #ababab;
		margin-bottom: 10px; }
	&__item {
		font-size: 14px; }
	&__label {
		font-size: 14px;
		font-weight: 700;
		margin-right: 5px; }
	&__descr {
		p {
			color: #000000;
			font-size: 14px;
			line-height: 20px; } }
	&__reservation {
		margin-right: 31px; }
	&__services {
		display: flex;
		align-items: center;
		@include sm {
			order: -1;
			margin-bottom: 15px; }
		ul {
			margin-bottom: 0; } }
	&__services-title {
		font-size: 14px;
		line-height: 20px;
		margin-right: 15px; }
	&__list-icon {
		display: flex;
		align-items: center;
		flex-wrap: wrap; }
	&__item-icon {
		&:not(:last-child) {
			margin-right: 15px; } }
	&__btn {
		text-align: center; }
	&__price-sm {
		color: #000000;
		font-size: 21px;
		line-height: 20px;
		display: none;
		margin-bottom: 15px;
		@include sm {
			display: block; } } }
