  padding: 88px 44px;
  min-height: 305px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 0 0 10px 10px;
  background-image: linear-gradient(-17deg, #2e4281 0%, #415db5 100%);
}

