  }
}

.slider .owl-dots {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  bottom: 22px;
  font-size: 0;
  text-align: center;
}

.slider .owl-dots button {
  position: relative;
  width: 18px;
  height: 18px;
  background-color: #fff;
  border-radius: 50%;
  display: inline-block;
  vertical-align: top;
}

.slider .owl-dots button:not(:first-child) {
  margin-left: 13px;
}

.slider .owl-dots button:before {
  position: absolute;
  content: "";
  top: 4px;
  left: 4px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #3d57aa;
  opacity: 0;
}

.slider .owl-dots button.active:before {
  opacity: 1;
}
