$icons: (0:0);
$icons: map-merge($icons,('arrow-big-left': (X: 0px, Y:0px, W: 66px, H: 110px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('arrow-big-right': (X: -76px, Y:0px, W: 66px, H: 110px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('facebook': (X: -234px, Y:-48px, W: 10px, H: 19px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('facebook@2x': (X: -234px, Y:0px, W: 20px, H: 38px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('ordinator': (X: -193px, Y:-72px, W: 28px, H: 28px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('phone@2x': (X: -152px, Y:0px, W: 72px, H: 62px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('reservation-gr': (X: -140px, Y:-120px, W: 33px, H: 26px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('reservation': (X: 0px, Y:-120px, W: 60px, H: 60px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('safe': (X: -152px, Y:-72px, W: 31px, H: 31px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('tv': (X: 0px, Y:-190px, W: 26px, H: 28px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('wi-fi': (X: -183px, Y:-120px, W: 30px, H: 23px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('wi-fi@2x': (X: -70px, Y:-120px, W: 60px, H: 46px, TW: 254px, TH: 218px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y);
  display: inline-block; }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i);
  display: inline-block; }

